'use client'; // Ïù¥ Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ Ï∏°ÏóêÏÑú Î†åÎçîÎßÅÎêòÎèÑÎ°ù ÏßÄÏ†ï

import Link from 'next/link';
import { useState, useEffect } from 'react';
// üí° AxiosÎ•º ÏÇ¨Ïö©ÌïòÎ†§Î©¥ Ïù¥ ÎùºÏù¥Î∏åÎü¨Î¶¨Í∞Ä ÌîÑÎ°úÏ†ùÌä∏Ïóê ÏÑ§ÏπòÎêòÏñ¥ ÏûàÏñ¥Ïïº Ìï©ÎãàÎã§.
// (npm install axios ÎòêÎäî yarn add axios)
import axios from 'axios'; 

// ----------------------------------------------------------------------
// ÌÉÄÏûÖ Ï†ïÏùò (Î∞±ÏóîÎìú ÏùëÎãµ ÌòïÌÉúÏóê Îî∞Îùº ÌïÑÏöîÌïòÎã§Î©¥ ÏàòÏ†ïÌïòÏÑ∏Ïöî)
interface User {
  name: string;
  isLoggedIn: boolean;
}
// ----------------------------------------------------------------------

export default function HomePage() {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // üí° Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞ Î°úÏßÅ (ÏàòÏ†ïÎêú Axios ÏöîÏ≤≠ Í≤ΩÎ°ú Ï†ÅÏö©)
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        // ‚úÖ API ÏöîÏ≤≠ Í≤ΩÎ°ú ÏàòÏ†ï: '/api/profile'Î°ú ÏãúÎèÑ (Í∞ÄÏû• ÏùºÎ∞òÏ†ÅÏù∏ ÌòïÌÉú)
        // Î∞±ÏóîÎìúÏóêÏÑú Ï†ïÏùòÌïú Ï†ïÌôïÌïú ÏóîÎìúÌè¨Ïù∏Ìä∏Í∞Ä Î¨¥ÏóáÏù∏ÏßÄ ÌôïÏù∏ ÌõÑ ÌïÑÏöî Ïãú ÏàòÏ†ïÌïòÏÑ∏Ïöî.
        const response = await axios.get('/api/profile'); 
        
        // API ÏùëÎãµ Îç∞Ïù¥ÌÑ∞ Íµ¨Ï°∞Ïóê Îî∞Îùº 'response.data.name'ÏùÑ ÏàòÏ†ïÌï¥Ïïº Ìï† Ïàò ÏûàÏäµÎãàÎã§.
        setUser({ 
          name: response.data.name || 'User', 
          isLoggedIn: true 
        });
      } catch (error) {
        // 404, 401 Îì±Ïùò ÏóêÎü¨ Î∞úÏÉù Ïãú Î°úÍ∑∏ÏïÑÏõÉ ÏÉÅÌÉúÎ°ú Í∞ÑÏ£º
        console.warn("User data fetch failed (Setting to logged out state):", error);
        setUser({ name: '', isLoggedIn: false });
      } finally {
        setIsLoading(false);
      }
    };

    fetchUserData();
  }, []);

  // ----------------------------------------------------------------------
  // Ìó§ÎçîÏùò Î°úÍ∑∏Ïù∏/ÌîÑÎ°úÌïÑ ÎßÅÌÅ¨Î•º Ï°∞Í±¥Î∂ÄÎ°ú Î†åÎçîÎßÅÌïòÎäî Ìï®Ïàò
  const renderAuthLinks = () => {
    if (isLoading) {
      return <div className="text-gray-500 text-sm">Loading...</div>;
    }

    if (user?.isLoggedIn) {
      return (
        <Link 
          href="/my/profile" 
          className="text-white bg-indigo-600 hover:bg-indigo-700 px-3 py-2 rounded-md text-sm font-medium"
        >
          {user.name} ÎãòÏùò ÌîÑÎ°úÌïÑ
        </Link>
      );
    }

    return (
      <>
        <Link 
          href="/login" 
          className="text-gray-500 hover:bg-gray-100 hover:text-gray-900 px-3 py-2 rounded-md text-sm font-medium"
        >
          Î°úÍ∑∏Ïù∏
        </Link>
        <Link 
          href="/register" 
          className="text-white bg-indigo-600 hover:bg-indigo-700 px-3 py-2 rounded-md text-sm font-medium"
        >
          ÌöåÏõêÍ∞ÄÏûÖ
        </Link>
      </>
    );
  };
  // ----------------------------------------------------------------------

  return (
    <div className="bg-gray-50 min-h-screen flex flex-col">
      {/* Header */}
      <header className="bg-white shadow-sm">
        <nav className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex-shrink-0">
              <Link href="/" className="text-2xl font-bold text-indigo-600">
                CaravanShare
              </Link>
            </div>
            <div className="hidden md:block">
              <div className="ml-10 flex items-baseline space-x-4">
                <Link href="/" className="text-gray-500 hover:bg-gray-100 hover:text-gray-900 px-3 py-2 rounded-md text-sm font-medium">
                  Home
                </Link>
                <Link href="/caravans" className="bg-indigo-100 text-indigo-700 hover:bg-indigo-200 px-3 py-2 rounded-md text-sm font-medium">
                  Explore Caravans
                </Link>
                {renderAuthLinks()} {/* ‚úÖ Ïù∏Ï¶ù/ÌîÑÎ°úÌïÑ ÎßÅÌÅ¨ Ï∂îÍ∞Ä */}
              </div>
            </div>
          </div>
        </nav>
      </header>

      {/* Main Content */}
      <main className="flex-grow">
        <div className="max-w-7xl mx-auto py-16 px-4 sm:px-6 lg:px-8 text-center">
          <h1 className="text-4xl font-extrabold text-gray-900 sm:text-5xl md:text-6xl">
            <span className="block">Find Your Next</span>
            <span className="block text-indigo-600">Adventure on Wheels</span>
          </h1>
          <p className="mt-3 max-w-md mx-auto text-base text-gray-500 sm:text-lg md:mt-5 md:text-xl md:max-w-3xl">
            Rent unique caravans from trusted hosts around the world. Your next journey awaits.
          </p>
          <div className="mt-5 max-w-md mx-auto sm:flex sm:justify-center md:mt-8">
            <div className="rounded-md shadow">
              <Link
                href="/caravans"
                className="w-full flex items-center justify-center px-8 py-3 border border-transparent text-base font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 md:py-4 md:text-lg md:px-10"
              >
                Explore Caravans
              </Link>
            </div>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-white">
        <div className="max-w-7xl mx-auto py-12 px-4 sm:px-6 lg:px-8">
          <p className="text-center text-base text-gray-400">
            &copy; {new Date().getFullYear()} CaravanShare. All rights reserved.
          </p>
        </div>
      </footer>
    </div>
  );
}